import type { Meta, StoryObj } from '@storybook/web-components';
import { html } from 'lit';
import { TpOffline } from '../offline';
import { TpOfflineType } from '../utils/offline.type';

const meta: Meta<TpOffline & TpOfflineType> = {
  title: 'components/Core/Offline',
  argTypes: {
    offlineTitle: {
      control: { type: 'text' },
      description: 'Define o titulo do componente.',
      table: {
        defaultValue: {
          summary: '',
        },
        type: {
          summary: 'text',
        },
      },
    },
    offlineText: {
      control: { type: 'text' },
      description: 'Define o texto do componente.',
      table: {
        defaultValue: {
          summary: '',
        },
        type: {
          summary: 'text',
        },
      },
    },
  },
  args: {
    offlineTitle: 'Você está offline',
    offlineText: 'Conecte-se à internet para visualizar esse conteúdo',
  },
};

export default meta;

type Story = StoryObj<TpOffline & TpOfflineType>;

export const Default: Story = {
  render: ({ ...args }) => {
    return html`
      <tp-offline
        offline-title=${args.offlineTitle}
        offline-text=${args.offlineText}
      ></tp-offline>
    `;
  },
};
Default.argTypes = {
  color: { table: { disable: true } },
};
